import { useState } from "react";
import { useNavigate } from "react-router-dom";

const Login = () => {
  const [emailFocused, setEmailFocused] = useState(false);
  const [pwFocused, setPwFocused] = useState(false);
  const [emailError,setEmailError] = useState(false);
  const [pwError,setPwError] = useState(false);
  const [isCode,setIsCode] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = (event) => {
    event.preventDefault();
  }
  return (
    <div className="login">
      <div className="login-popup">
        <p className="close" onClick={()=>{navigate("/")}}></p>
        <h2></h2>
        <form onSubmit={handleSubmit}>
          <label className={`${emailFocused ? "focused" : ""} ${emailError ? "error" : ""}`}>
            <span>    </span>
            <input type="text"
              onFocus={()=>{
                setEmailFocused(true);
                setEmailError(false);
              }}
              onBlur={(event)=>{
              if(event.target.value === ""){
                setEmailFocused(false);
                setEmailError(true);
              }
              }}
            />
          </label>
          {
              emailError && <p className="input-error">     .</p>
          }
          {
            !isCode && (<label className={`${pwFocused ? "focused" : ""} ${pwError ? "error" : ""}`}>
            <span></span>
            <input type="password" 
            onFocus={()=>{
              setPwFocused(true);
              setPwError(false);
            }}
            onBlur={(event)=>{
              if(event.target.value === ""){
                setPwFocused(false);
              }
              if(event.target.value.length < 4){
                setPwError(true);
              }
            }}
            />
          </label>)
          }
          {
              pwError && <p className="input-error">  4~60  .</p>
          }
          {
            isCode && <p>      .</p>
          }
          <button type="submit">
            { isCode ? "  " : "" }
          </button>
          <p></p>
          <button onClick={()=>{
            setIsCode(!isCode);
            setEmailError(false);
            setPwError(false);
          }}>
            { isCode ? " " : "  " }
          </button>
          <a href="#">
            { isCode ? "   ?" : " ?" }
          </a>
        </form>
        <div className="login-box">
          <label>
            <input type="checkbox"/>
            <span>  </span>
          </label>
          <p>Netflix  ? <a href="#"> .</a></p>
          <p>  Google reCAPTCHA      .
            <a href="#"> .</a>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login;